.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 20;
    background-color: rgba(219, 216, 216, 0.75);
    opacity: 0.8;
}

.modal {
    position: fixed;
    top: 70vh;
    left: calc(50% + 20rem);
    width: 30em;
    background-color: white;
    padding: 1rem;
    border-radius: 14px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 30;
    animation: slide-down 300ms ease-out forwards;
}

.submitModal {
    position: fixed;
    top: 25vh;
    left: calc(50% - 20rem);
    width: 40em;
    background-color: white;
    padding: 1rem;
    border-radius: 14px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 30;
    animation: slide-down 300ms ease-out forwards;
}

.submitModalEnroll {
    position: fixed;
    top: 25vh;
    left: calc(50% - 20rem);
    width: 35em;
    background-color: white;
    padding: 1rem;
    border-radius: 14px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 30;
    animation: slide-down 300ms ease-out forwards;
}

